                <input type="range" min="0" :max="duration" v-model="currentTime" @input="seekAudio" />
            </div>
            <div class="d-flex pb-5 pt-2">
                <img src="../assets/Atras.png" class="m-auto" @click="prevTrack">
                <img :src="isPlaying ? pauseIcon : playIcon" style="height: 100%; width: fit-content; margin: auto;"
                    @click="togglePlay">
                <img src="../assets/Siguiente.png" class="m-auto" @click="nextTrack">
                <button @click="toggleShuffle" :class="{ active: isShuffle }" class="shuffle-button m-auto">
                    <img v-show="!isShuffle" src="../assets/Aleatorio-dis.png" class="m-auto" width="35">
                    <img v-show="isShuffle" src="../assets/Aleatorio-ava.png" class="m-auto" width="35">
                </button>
            </div>
        </div>
        <div class="fullblack py-1">
            <p class="sb-20-white text-start" v-html="t('audioteca.audioejercicios')" >
               
            </p>
            <div style="position: absolute; right: 20px; margin-top: -20px;">
                <img src="../assets/heart.png" class="m-auto">
            </div>
        </div>
        <br>
        <!-- Lista de canciones -->
        <ul class="px-3 pb-5">
            <li v-for="(song, index) in songs" :key="song.id" draggable="true" @dragstart="dragStart(index)"
                @dragover.prevent @drop="drop(index)" class="lisongs b-16-gray btn-group" role="group">
                <div>
                    <canvas v-show="index === currentIndex && isPlaying" :ref="el => setMiniCanvasRef(el, index)"
                        width="40" height="25" class="mini-visualizer px-1"></canvas>
                    <button style="width: 48px;" v-show="index != currentIndex" class="btn-audio"
                        @click="playSong(index)">
                        <img src="../assets/Play.png" width="12" class="m-auto"></button>
                </div>
                <span @click="playSong(index)" style="flex: 1; cursor: pointer;" class="text-start span-audio">
                    <label disabled v-if="editingIndex !== index" value="">{{ song.name }}</label>
                    <input v-else v-model="editedName" @keyup.enter="saveEdit(index)" @blur="saveEdit(index)" />
                </span>
                <!-- Mini canvas solo para la canción actual -->

                <button class="btn-audio" @click="editSong(index)"> <img src="../assets/Edit.png"
                        class="m-auto"></button>
                <button class="btn-audio" @click="removeSong(index)"> <img src="../assets/Delete-song.png"
                        class="m-auto"></button>
                <!-- <button class="btn-audio" @click="moveUp(index)"> <img width="25" src="../assets/up-b.svg"
                        class="m-auto"></button>
                <button class="btn-audio" @click="moveDown(index)"> <img width="25" src="../assets/up-b.svg"
                        class="m-auto svg"></button> -->
            </li>
        </ul>
    </div>

    <div v-show="moment">
        <div>
            <div>
                <!-- <label class="sb-20-black pb-4">¿En qué momento estabas?</label>
                <ul class="ps-0">
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-m" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-m" class="form-check-label">1) Antes de actividades previstas, planeadas o
                                rutinarias. </label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-s" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-s" class="form-check-label">2) Cavilaciones y sobrepensamiento. Encerrado
                                en tu
                                mente.</label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-a" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-a" class="form-check-label">3) Transiciones entre situaciones, actividades,
                                lugares. </label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-4" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-4" class="form-check-label">4) Indignación o irritación por palabras o
                                acciones
                                de alguien más.</label>
                        </div>
                    </li>
                    <li class="list-group-item text-start r-16-gray py-2">
                        <div class="ps-5 form-check">
                            <input id="plan-5" class="form-check-input" type="radio" name="plan" value=""
                                aria-label="...">
                            <label for="plan-5" class="form-check-label">5) Después de hablar/actuar de manera
                                emocional, impulsiva e
                                incorrecta.</label>
                        </div>
                    </li>

                </ul> -->
                <div class="d-flex justify-content-center pt-3 pb-5">
                    <div class="px-3">
                        <button type="button" class="btn-gray-white" @click="SetMoment()">
                            Omitir
                        </button>
                    </div>
                    <div class="px-3">
                        <button type="button" class="btn-black border-0" @click="SetMoment()">
                            Guardar
                        </button>
                    </div>
                </div>
            </div>
            <!-- <div>
                <label class="sb-20-black pb-5">¿Por qué indicar el momento?</label>
                <p class="r-16-gray text-center px-3 pb-5">
                    Distinguir y registrar el “momento” en el que estabas cuando decidiste hacer un audioejercicio(s) te
                    ayudará a:
                    <br><br>
                    - Entender cuándo debes estar más atento para regular tus emociones pre-ira y contener tus
                    principales
                    detonantes de ira.
                    <br><br>
                    - Desarrollar una mayor capacidad de prevención y autocontrol.
                    <br><br>
                    Si decides no indicar el momento, solo pulsa el botón “omitir” cuando aparezca la pantalla de
                    registro.
                </p>
            </div> -->
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, computed } from 'vue'
import { useI18n } from 'vue-i18n'
    
  const { t } = useI18n()


const songs = ref([
    { id: 1, name: 'AP 1', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_2.mp3', import.meta.url).href },
    { id: 2, name: 'AP 2', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_6.mp3', import.meta.url).href },
    { id: 3, name: 'AP 3', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_7.mp3', import.meta.url).href },
    { id: 4, name: 'AP 4', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_8.mp3', import.meta.url).href },
    { id: 5, name: 'AP 5', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_9.mp3', import.meta.url).href },
    { id: 6, name: 'AP 6', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_11.mp3', import.meta.url).href },
    { id: 7, name: 'AP 7', url: new URL('@/assets/audios/Angerpulse_Audioejercicio_21.mp3', import.meta.url).href }
])

const playIcon = new URL('@/assets/Play.png', import.meta.url).href
const pauseIcon = new URL('@/assets/Pausa.png', import.meta.url).href

const currentIndex = ref(0)
const moment = ref(false)

const isPlaying = ref(false)
const isShuffle = ref(true)
const currentTime = ref(0)
const duration = ref(0)
const audio = new Audio()
const countPause = ref(0)

const hasShownMessage = ref(false)
let playbackInterval = null
const playbackSeconds = ref(0)

const canvasRef = ref(null)
let audioContext, analyser, dataArray, source, animationId
let shuffledIndices = []

const currentSongName = computed(() => songs.value[currentIndex.value]?.name || '')

function setupVisualizer() {
    audioContext = new (window.AudioContext || window.webkitAudioContext)()
    analyser = audioContext.createAnalyser()
    analyser.fftSize = 256
    const bufferLength = analyser.frequencyBinCount
    dataArray = new Uint8Array(bufferLength)

    source = audioContext.createMediaElementSource(audio)
    source.connect(analyser)
    analyser.connect(audioContext.destination)

    drawVisualizer()

    if (audioContext.state === 'suspended') {
        const resume = () => {
            audioContext.resume()
            document.removeEventListener('touchstart', resume)
        }
        document.addEventListener('touchstart', resume, { once: true })
    }
}

function drawVisualizer() {
    const mainCanvas = canvasRef.value
    const mainCtx = mainCanvas.getContext('2d')
    const width = mainCanvas.width
    const height = mainCanvas.height
    const centerY = height / 2

    function draw() {
        animationId = requestAnimationFrame(draw)
        analyser.getByteFrequencyData(dataArray)

        // Canvas principal
        mainCtx.clearRect(0, 0, width, height)
        const barWidth = width / dataArray.length
        let x = 0
        for (let i = 0; i < dataArray.length; i++) {
            const value = dataArray[i]
            const barHeight = value / 2
            const gradient = mainCtx.createLinearGradient(0, centerY - barHeight, 0, centerY + barHeight)
            gradient.addColorStop(0, '#00f')
            gradient.addColorStop(1, '#0ff')
            mainCtx.fillStyle = gradient
            mainCtx.fillRect(x, centerY - barHeight, barWidth, barHeight)
            mainCtx.fillRect(x, centerY, barWidth, barHeight)
            x += barWidth + 1
        }

        // Mini visualizador (solo para la canción actual)
        const miniCanvas = miniCanvasRefs.value[currentIndex.value]
        if (miniCanvas) {
            const miniCtx = miniCanvas.getContext('2d')
            miniCtx.clearRect(0, 0, miniCanvas.width, miniCanvas.height)
            const miniBarWidth = miniCanvas.width / dataArray.length
            let mx = 0
            for (let i = 0; i < dataArray.length; i++) {
                const value = dataArray[i]
                const barHeight = value / 3
                miniCtx.fillStyle = '#0af'
                miniCtx.fillRect(mx, miniCanvas.height - barHeight, miniBarWidth, barHeight)
                mx += miniBarWidth + 1
            }
        }
    }

    draw()
}

function shuffleArray(length) {
    const indices = [...Array(length).keys()]
    for (let i = indices.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1))
            ;[indices[i], indices[j]] = [indices[j], indices[i]]
    }
    return indices
}
function SetMoment() {
  moment.value = false
}

function loadTrack(index) {
    const song = songs.value[index]
    if (!song) return
    audio.src = song.url
    audio.load()
    // playbackSeconds.value = 0
     stopPlaybackTimer()
    if (isPlaying.value == true) {
        audio.play().then(() => {
            isPlaying.value = true
            startPlaybackTimer()
        }).catch(err => {
            console.warn('iOS puede haber bloqueado la reproducción:', err)
        })
    }
    else {
        isPlaying.value = false
    }
}

function togglePlay() {
    console.log(playbackSeconds.value);
    console.log(currentTime.value);
    if (audio.paused) {
        audio.play()
        isPlaying.value = true
        startPlaybackTimer()
    } else {
        audio.pause()
        isPlaying.value = false
        stopPlaybackTimer()
        countPause.value ++;
         if (playbackSeconds.value >= 110 | (playbackSeconds.value >= 107 && countPause.value > 2) ) {
    //if (playbackSeconds.value >= 110 || (playbackSeconds.value >= 106 && currentTime.value >= 110) ) {
            moment.value = true;
            playbackSeconds.value = 0;
            hasShownMessage.value = true
            countPause.value = 0
        }
    }
}

function startPlaybackTimer() {
    if (playbackInterval) return // Evita múltiples intervalos

    playbackInterval = setInterval(() => {
        playbackSeconds.value++
    }, 1000)
}

function stopPlaybackTimer() {
    clearInterval(playbackInterval)
    playbackInterval = null
}

function nextTrack() {
    if (isShuffle.value) {
        if (shuffledIndices.length === 0) {
            shuffledIndices = shuffleArray(songs.value.length)
        }
        currentIndex.value = shuffledIndices.shift()
    } else {
        currentIndex.value = (currentIndex.value + 1) % songs.value.length
    }
    isPlaying.value = true
    loadTrack(currentIndex.value)
}

function prevTrack() {
    if (isShuffle.value) {
        if (shuffledIndices.length === 0) {
            shuffledIndices = shuffleArray(songs.value.length)
        }
        currentIndex.value = shuffledIndices.shift()
    } else {
        currentIndex.value = (currentIndex.value - 1 + songs.value.length) % songs.value.length
    }
    isPlaying.value = true
    loadTrack(currentIndex.value)
}

function toggleShuffle() {
    isShuffle.value = !isShuffle.value

    if (isShuffle.value) {
        shuffledIndices = shuffleArray(songs.value.length)
        // Evita repetir la canción actual
        shuffledIndices = shuffledIndices.filter(i => i !== currentIndex.value)
    } else {
        shuffledIndices = []
    }
}

function seekAudio() {
    audio.currentTime = currentTime.value
}

audio.addEventListener('timeupdate', () => {
    currentTime.value = audio.currentTime
    duration.value = audio.duration || 0
})

audio.addEventListener('ended', () => {
    nextTrack()
})

function formatTime(time) {
    const minutes = Math.floor(time / 60)
    const seconds = Math.floor(time % 60).toString().padStart(2, '0')
    return `${minutes}:${seconds}`
}

onMounted(() => {
    setupVisualizer()
    loadTrack(currentIndex.value)
})

onBeforeUnmount(() => {
    cancelAnimationFrame(animationId)
    audio.pause()
    audio.src = ''
    if (audioContext) audioContext.close()
})



const editingIndex = ref(null)
const editedName = ref('')

function playSong(index) {
    isPlaying.value = true
    currentIndex.value = index
    loadTrack(index)
    startPlaybackTimer()
    isPlaying.value = true
}

function removeSong(index) {
    if (currentIndex.value === index) {
        audio.pause()
    }
    songs.value.splice(index, 1)
    if (currentIndex.value >= songs.value.length) {
        currentIndex.value = 0
    }
    loadTrack(currentIndex.value)
}

function editSong(index) {
    editingIndex.value = index
    editedName.value = songs.value[index].name
}

function saveEdit(index) {
    if (editedName.value.trim()) {
        songs.value[index].name = editedName.value.trim()
    }
    editingIndex.value = null
}

let draggedIndex = null

function dragStart(index) {
    draggedIndex = index
}

function drop(index) {
    if (draggedIndex === null || draggedIndex === index) return
    const movedItem = songs.value.splice(draggedIndex, 1)[0]
    songs.value.splice(index, 0, movedItem)
    draggedIndex = null
}

// function moveUp(index) {
//     if (index === 0) return
//     const temp = songs.value[index]
//     songs.value[index] = songs.value[index - 1]
//     songs.value[index - 1] = temp
//     if (currentIndex.value === index) currentIndex.value = index - 1
//     else if (currentIndex.value === index - 1) currentIndex.value = index
// }

// function moveDown(index) {
//     if (index === songs.value.length - 1) return
//     const temp = songs.value[index]
//     songs.value[index] = songs.value[index + 1]
//     songs.value[index + 1] = temp
//     if (currentIndex.value === index) currentIndex.value = index + 1
//     else if (currentIndex.value === index + 1) currentIndex.value = index
// }


const miniCanvasRefs = ref({})

function setMiniCanvasRef(el, index) {
    if (el) miniCanvasRefs.value[index] = el
    else delete miniCanvasRefs.value[index]
}

</script>

