    border-left: 1px solid #D6D6D6;
    padding: 7px 10px;
}

.span-audio {
    background-color: #F4F4F4;
    padding-top: 0px;
    padding-bottom: 0px;
    border-left: 1px solid #D6D6D6;
}

.span-audio>input {
    border: none;
    width: 100%;
    padding-left: 10px;
}

.span-audio>input:focus {
    border: none;
    width: 100%;
    padding-left: 10px;
    outline: none;
}

.span-audio>label {
    padding-top: 7px;
    padding-bottom: 7px;
    padding-left: 10px;
}

.svg {
    transform: rotate(180deg);
}
</style>
